import React from "react";

import { Title, MainWrapper } from "../components/Headings";

export default class Index extends React.Component {
  render() {
    return (
      <MainWrapper color="#2980b9">
        <Title>Contact</Title>
        <p>Please feel free to contact me through the email below.</p>
        <p className="cont">
          <a
            href="&#x6D;&#x61;&#x69;&#x6C;&#x74;&#x6F;&#x3A;&#x6D;&#x2E;&#x70;&#x6F;&#x6C;&#x69;&#x6E;&#x6F;&#x77;&#x73;&#x6B;&#x69;&#x40;&#x67;&#x6D;&#x61;&#x69;&#x6C;&#x2E;&#x63;&#x6F;&#x6D;"
            className="email"
          >
            &#x6D;&#x2E;&#x70;&#x6F;&#x6C;&#x69;&#x6E;&#x6F;&#x77;&#x73;&#x6B;&#x69;&#x40;&#x67;&#x6D;&#x61;&#x69;&#x6C;&#x2E;&#x63;&#x6F;&#x6D;
          </a>
        </p>
      </MainWrapper>
    );
  }
}



